<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">综治机构人数区域分布</div>
                <div id="PeopleCount" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="8">
                <techo-card resizeEvent="bottomLeftCard" :maxCard="true">
                    <div slot="header">综治机构人数层级分布</div>
                    <div id="PeopleLevel" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8">
                <techo-card resizeEvent="bottomMidCard" :maxCard="true">
                    <div slot="header">综治机构人员学历分布占比</div>
                    <div id="Education" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="8" resizeEvent="bottomRightCard">
                <techo-card>
                    <div slot="header">综治机构人员政治面貌分布占比</div>
                    <div id="PoliticsStatus" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 综治机构人数区域分布
      topBarOptions: {},
      topBarChart: null,
      // 综治机构人数层级分布
      bottomLeftOptions: {},
      bottomLeftChart: null,
      // 综治机构人员学历分布占比
      bottomMidOptions: {},
      bottomMidChart: null,
      // 综治机构人员政治面貌分布占比
      bottomRightOptions: {},
      bottomRightChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 综治机构人数区域分布
    this.topBarChart = this.$echarts.init(
      this.$el.querySelector("#PeopleCount")
    );
    // 综治机构人数层级分布
    this.bottomLeftChart = this.$echarts.init(
      this.$el.querySelector("#PeopleLevel")
    );
    // 综治机构人员学历分布占比
    this.bottomMidChart = this.$echarts.init(
      this.$el.querySelector("#Education")
    );
    // 综治机构人员政治面貌分布占比
    this.bottomRightChart = this.$echarts.init(
      this.$el.querySelector("#PoliticsStatus")
    );

    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getOrgData(val.data.rawdata.code);
        this.getOrgPieData(val.data.rawdata.code);
        this.getPeoplePieData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("bottomLeftCard", () => {
      that.bottomLeftChart.resize();
    });
    this.$bus.$on("bottomMidCard", () => {
      that.bottomMidChart.resize();
    });
    this.$bus.$on("bottomRightCard", () => {
      that.bottomRightChart.resize();
    });
  },
  created() {
    /** 综治机构人数区域分布 */
    this.getOrgData();
    /** 综治机构人数层级分布 */
    this.getLevelOrgData();
    /** 综治机构人员学历分布占比 */
    this.getOrgPieData();
    /** 综治机构人员政治面貌分布占比 */
    this.getPeoplePieData();
  },

  methods: {
    // 综治机构人数区域分布
    getOrgData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewCompreOrgTeam",
        fieldName: "orgType"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityAndField(param, data => {
        if (data.length == 0) return;
        that.byCode = data[0].code;
        that.topBarOptions.yAxis.name = "人数";
        let legend = _.keys(_.groupBy(data, "keyName"));
        that.topBarOptions.legend.data = legend;
        _.map(legend, (value, index) => {
          that.topBarOptions.series.push({
            name: value,
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[index]
              }
            },
            barMaxWidth: 80,
            data: []
          });
        });
        _.map(_.groupBy(data, "name"), (item, key) => {
          that.topBarOptions.xAxis.data.push(key);
          _.map(item, value => {
            let serie = _.find(that.topBarOptions.series, name => {
              return name.name == value.keyName;
            });
            serie.data.push({
              rawdata: value,
              value: value.count
            });
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 综治机构人数层级分布
    getLevelOrgData() {
      // P地市 R区县 S乡镇 T社区 U网格
      let param = {
        entityName: "ViewCompreOrgTeam",
        fieldName: "orgType"
      };
      let that = this;
      this.bottomLeftOptions = _.cloneDeep(barOptions);
      service.requestByLevelField(param, data => {
        this.bottomLeftOptions.yAxis.name = "人数";
        let legend = _.keys(_.groupBy(data, "keyName"));
        this.bottomLeftOptions.legend.data = legend;
        _.map(legend, (value, index) => {
          that.bottomLeftOptions.series.push({
            name: value,
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[index]
              }
            },
            barMaxWidth: 80,
            data: []
          });
        });
        _.map(_.groupBy(_.sortBy(data, "name"), "name"), (item, key) => {
          let xItem = "";
          switch (key) {
            case "P":
              xItem = "市";
              break;
            case "R":
              xItem = "区县";
              break;
            case "S":
              xItem = "乡镇";
              break;
            case "T":
              xItem = "社区";
              break;
            case "U":
              xItem = "网格";
              break;
            default:
              break;
          }
          that.bottomLeftOptions.xAxis.data.push(xItem);
          _.map(item, value => {
            let serie = _.find(that.bottomLeftOptions.series, name => {
              return name.name == value.keyName;
            });
            serie.data.push(value.count);
          });
        });
        this.bottomLeftChart.setOption(that.bottomLeftOptions);
        this.bottomLeftChart.resize();
      });
    },
    // 综治机构人员学历分布占比
    getOrgPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewCompreOrgTeam",
        fieldName: "education"
      };
      let that = this;
      this.bottomMidOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.bottomMidOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.bottomMidOptions.title.text = " ";
        that.bottomMidOptions.series[0].name = "综治队伍";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.bottomMidOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.bottomMidOptions.series[0].radius = "45%";
        that.bottomMidOptions.series[0].center = ["65%", "50%"];
        that.bottomMidChart.setOption(that.bottomMidOptions);
        that.bottomMidChart.resize();
      });
    },

    // 综治机构人员政治面貌分布占比
    getPeoplePieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewCompreOrgTeam",
        fieldName: "politicsStatus"
      };
      let that = this;
      this.bottomRightOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.bottomRightOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.bottomRightOptions.title.text = " ";
        that.bottomRightOptions.series[0].name = "综治队伍";
        data = data.filter(item => item.count != 0);
        _.map(data, item => {
          that.bottomRightOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.bottomRightOptions.series[0].radius = "45%";
        that.bottomRightOptions.series[0].center = ["65%", "50%"];
        that.bottomRightChart.setOption(that.bottomRightOptions);
        that.bottomRightChart.resize();
      });
    },

    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getOrgData(districtCode);
      this.getOrgPieData(districtCode);
      this.getPeoplePieData(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 20rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 21.2rem);
    width: 100%;
    .el-col-8 {
      height: 100%;
    }
  }
}
</style>